import { RouteComponentProps } from "react-router-dom";
import Document from "components/Document";

type DocumentParams = { userId: string };
export interface UserDocumentProps extends RouteComponentProps<DocumentParams> {}

const UserDocument: React.FC<UserDocumentProps> = ({ match }) => {
  const userId = match.params.userId;

  return <Document docId={userId} collectionId="UsersReservedCollection"></Document>;
};

export default UserDocument;
